<template>
  <div class="tab_type_box">
    <div class="tab_type_head">
      <a class="active"></a>
      <p class="company_a">
        <span @click="claim()"></span>
        <i>|</i>
        <span @click="claim()"></span>
      </p>
    </div>
    <div class="padding20">
      <companyList></companyList>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import companyList from '../companyList/companyList'

  export default {
    components:{
      companyList
    },
    methods:{
      claim:function(){
        window.location.href="#/manage/personal/myCompany/myCompanyClaim";
      }
    }
  }
</script>

<style media="screen">
    .company_a>span{
        color: #0066FF;
        font-size: 14px!important;
        transition: .3s;
        cursor: pointer
    }
    .company_a>span:hover{
        color: #4b93ff
    }
    .company_a>i{
        color: #E0E3EB;
        font-size: 12px;
        margin: 0 10px
    }
</style>



