<template>
  <div class="tab_type_box">
    <div class="tab_type_head">
      <a class="active"></a>
    </div>
    <div class="padding20">
      <section class="help_select">
        <input type="text" name="" value="" placeholder="">
        <span class="help_select_btn"><span class="icon-search"></span> </span>
      </section>
      
      
      <!-- begin-->
      <section class="company-search-no">
          
          <span></span>
          <a href="#" class="btn-bd-green-s"></a>
      </section>
      <!-- end-->
      
      
      <companyList></companyList>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import companyList from '../companyList/companyList'

  export default {
    components:{
      companyList
    }
  }
</script>

<style media="screen">
  .help_select{
    display: flex;
    width: 550px;
    height: 32px;
    border: 2px solid #0065FF;
    margin: 30px auto 35px auto;
  }
  .help_select input{
    flex-grow: 1;
    border: 0;
    padding-left: 10px;
  }
  .help_select_btn{
    width: 80px;
    height: 32px;
    background: #0065FF;
    color: #fff;
    font-size: 15px;
    text-align: center;
    line-height: 32px;
      transition: .3s;
      cursor: pointer
  }
    .help_select_btn:hover{
        background: #3b8aff
    }
    .company-search-no{
        text-align: center;
        font-size: 14px;
        padding: 30px 0
    }
    .company-search-no>span{
        color: #AAAAAA;
    }
</style>



